.site-title-container {
    display: flex;
    position: relative;
    overflow: hidden;
    padding: 5px;
    width: 80%;
}
.site-title-container{
    .site-title, .site-title:visited {
        color:#333;
    }
}

.site-footer {
    background-color: #3C3333;
}

.site-title-container::before,.site-title-container::after {
    width: 80px;
    height: 50px;
    content: '';
    transform: rotateZ(-60deg);
    position: absolute;
    background-color: white;
}

.site-title-container::before {
    width: 110px;
    background-color: red;
    right: 44px;
    top: 9px;
}
.site-title-container::after {
    right: -32px;
    top: 16px;
}
.wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.site-header {
    padding: 0.2rem;
    border:none;
    border-bottom: 1px solid #333A;
}


